.main-section {
  display: flex;
  justify-content: center;
  align-items: center;
}

.cards-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
  row-gap: 20px;
  margin-top: 20px;

  @media screen and (min-width: 768px) {
    flex-direction: row;
    flex-wrap: wrap;
    padding: 32px;
    justify-content: center;
    align-items: center;
    width: 768px;
    gap: 32px;
  }

  @media screen and (min-width: 1268px) {
    width: 1220px;
    row-gap: 32px;
    column-gap: 16px;
    justify-content: flex-start;
    align-items: flex-start;
    padding: 0;
  }

  .film-box {
    list-style-type: none;
    width: 100%;
    height: 460px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    border-radius: 5px;

    @media screen and (min-width: 768px) {
      height: 495px;
      width: 336px;
    }

    @media screen and (min-width: 1268px) {
      height: 550px;
      width: 350px;
    }

    .film-info-container {
      margin-top: 5px;
      display: flex;
      flex-direction: column;
      gap: 5px;
      padding-left: 10px;
    }

    .film-img {
      width: 100%;
      height: 390px;
      border-radius: 5px;

      @media screen and (min-width: 768px) {
        height: 430px;
      }

      @media screen and (min-width: 1268px) {
        height: 490px;
      }
    }

    .film-name {
      font-family: var(--font-family);
      font-size: 20px;
      font-weight: 500;
      line-height: 24px;
      text-align: left;
      margin: 0;
    }

    .film-rating {
      color: rgba(185, 47, 44, 1);
      font-family: var(--font-family);
      font-size: 15px;
      font-weight: 500;
      line-height: 15px;
      letter-spacing: 0em;
      text-align: left;
      margin: 0;
      display: flex;
      align-items: center;
      gap: 5px;
      padding: 0;

      .rating-display {
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: orange;
        color: white;
        width: 40px;
        border-radius: 10px;
        height: 20px;
        margin: 0;
      }
    }
  }
}

.film-box:hover {
  box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.3), 0 4px 4px rgba(0, 0, 0, 0.3),
    1px 4px 6px rgba(0, 0, 0, 0.3);
  transition: box-shadow 0.3s;
  cursor: pointer;
}
